<template>
  <button @click.prevent="clicked" class="like-btn" v-b-tooltip.hover title="Добавить в избраное">
    <b-icon :style="styleIcon()" :icon="fill ? 'heart-fill' : 'heart'"></b-icon>
  </button>
</template>

<script>
import {mapActions, mapGetters, mapMutations} from "vuex";

export default {
  props: ['id', 'favourite'],
  data() {
    return {
      fill: this.favourite,
    }
  },
  methods: {
    ...mapGetters(['isAuthenticated']),
    ...mapMutations(['authWindow/setLogin', 'decreaseFavouriteCounter', 'increaseFavouriteCounter']),
    ...mapActions(['addRemoveFavourite']),
    clicked() {
      if (this.id && this.isAuthenticated()) {
        this.addRemoveFavourite({id: this.id, name: this.$route.name});
        this.fill = !this.fill;
        if (!this.fill) {
          this.$root.$emit("like-updated");
          this.decreaseFavouriteCounter();
        } else {
          this.increaseFavouriteCounter();
        }
      } else if (!this.isAuthenticated()) {
        this['authWindow/setLogin']();
      }
    },
    styleIcon() {
      return {
        color: this.fill ? 'red' : "grey"
      }
    }
  }
}
</script>
<style scoped>
button.like-btn {
  background: transparent;
  border-radius: 20px;
  z-index: 1;
  border: none;
  font-size: 18px;
}
</style>